import { useState } from "react";
import {
  Straight,
  Lesbian,
  Gay,
  ThreesomeFemale,
  ThreesomeMale,
  Group,
  Incest,
  Toys,
  Anal,
  TransGender,
  HotWife,
  BDSM,
  Rape,
  Cuckold,
} from "./UploadComponents";
import app from "../account/profile/contextconfig/Config";
import {
  getStorage,
  ref as refStorage,
  getDownloadURL,
  uploadBytesResumable,
} from "firebase/storage";
import { getDatabase, ref as refDb, set } from "firebase/database";
import { useHistory } from "react-router";
import { Burmese } from "../../components/burmese converter/BurmeseConverter";
import { Input, InputSubmit } from "../../components/form/Form";
import { FullScreenArrowGoBack } from "../../components/layout/MyLayout";
import { AlertWarning, ToastAlert } from "../../components/utilities/Utilities";
import { useAuth } from "../account/profile/contextconfig/AuthContext";

const initialValue = {
  title: "",
  content: "",
  type: "",
  originalWriter: "",
  mp3: "",
};

function Upload() {
  const [article, setArticle] = useState(initialValue);
  const [file, setFile] = useState("");
  const [spinner, setSpinner] = useState(false);
  const [feedback, setFeedback] = useState("");
  const { currentUser } = useAuth();
  const history = useHistory();
  const [warn, setWarn] = useState(false);

  function submitHandler(e) {
    e.preventDefault();
    setSpinner(true);
    setFeedback("");
    setWarn(false);

    if (article.type === "") {
      setSpinner(false);
      return setFeedback("အမ်ိဳးအစားမွာ အနည္းဆံုး တစ္ခု ေရြးေပးပါ");
    }

    let date = new Date();
    let id = date.getTime();

    const storage = getStorage();
    const db = getDatabase();
    const storageRef = refStorage(storage, "images/" + id + file.name);
    const uploadTask = uploadBytesResumable(storageRef, file);

    uploadTask.on(
      "state_changed",
      (snapshot) => {
        const progress =
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log("Upload is " + progress + "% done");
      },
      (error) => {
        console.log(error);
      },
      () => {
        getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
          const main = {
            title: article.title,
            content: article.content,
            originalWriter: article.originalWriter,
            mp3: article.mp3,
            img: downloadURL,
          };

          const titles = {
            title: article.title,
            like: { like: 0 },
            mp3: { mp3: false },
            id: id,
            type: article.type,
          };
          set(refDb(db, "titles/" + id), titles);
          set(refDb(db, "main/" + id), main).then(() => {
            history.goBack();
          });
        });
      }
    );
  }

  const changeHandler = (e) => {
    let name = e.target.name;
    if (e.target.checked) {
      setFeedback("");
      if (article.type.includes(name)) {
        return;
      } else {
        setArticle({ ...article, type: article.type + name + ", " });
      }
    } else {
      setArticle({ ...article, type: article.type.replace(name + ", ", "") });
    }
  };

  const handleChange = (e) => {
    setWarn(true);
    const { name, value } = e.target;
    setArticle({ ...article, [name]: value });
  };

  return (
    <FullScreenArrowGoBack prompt={warn} mode={true} zu={true}>
      <form className="" onSubmit={submitHandler}>
        <AlertWarning>
          <small>
            <Burmese
              str={
                "တူညီေသာ ဇာတ္လမ္းမ်ား ရွိၿပီးျဖစ္လွ်င္" +
                "သတ္ပံုမွားေသာ၊ mp3 မပါေသးေသာ၊ မျပည့္စံုေသာ ဇာတ္လမ္းကို ဖ်က္ပါမည္။"
              }
            />
          </small>
        </AlertWarning>
        <Input
          label={<Burmese str="ေခါင္းစဥ္" />}
          name="title"
          value={article.title}
          onChange={handleChange}
        />
        <label className="form-label" htmlFor="content">
          <Burmese str="ဇာတ္လမ္း (ျဖစ္ႏိုင္လွ်င္ အပုိင္းမ်ားခြဲ၍ မတင္ပါႏွင့္)" />
        </label>
        <div className="form-floating text-dark mb-3">
          <textarea
            name="content"
            maxLength="1000000"
            className="form-control"
            value={article.content}
            onChange={handleChange}
            style={{ height: "269px" }}
            id="content"
          />
          <label htmlFor="content text-danger">
            {article.content.length}/1000000
          </label>
        </div>

        {/* <InputArea
          rows="10"
          label={
            <Burmese str="ဇာတ္လမ္း (ျဖစ္ႏိုင္လွ်င္ အပုိင္းမ်ားခြဲ၍ မတင္ပါႏွင့္)" />
          }
          name="content"
          placeholder={"Maximum Character 1,000,000"}
          max="1000000"
          value={article.content}
          onChange={handleChange}
        /> */}
        <Input
          label={<Burmese str="မူရင္း ေရးသားသူ" />}
          name="originalWriter"
          value={article.originalWriter}
          onChange={handleChange}
        />

        {currentUser && currentUser.email === "whitedark@gmail.com" && (
          <Input
            req={false}
            label="mp3"
            name="mp3"
            value={article.mp3}
            onChange={handleChange}
          />
        )}

        <div className="mb-3">
          <label htmlFor="file" className="form-label">
            Image
          </label>
          <input
            accept="image/*"
            type="file"
            required={true}
            onChange={(e) => setFile(e.target.files[0])}
            id="file"
            placeholder="Choose an image"
            className="form-control"
          />
          {file && (
            <img
              style={{ width: "100%", maxWidth: "444px" }}
              src={URL.createObjectURL(file)}
              alt="current img"
            />
          )}
        </div>

        {/* <Input
          type="file"
          name="image"
          onChange={fileUpload}
          label={<Burmese str="ဓါတ္ပံု" />}
        /> */}

        <h6>
          <Burmese str="အမ်ိဳးအစား (အနည္းဆံုး တစ္ခုေရြးပါ)" />
        </h6>

        <div className="flex-column flex-md-row d-flex flex-wrap mb-3">
          <Straight onChange={changeHandler} />
          <Lesbian onChange={changeHandler} />
          <Gay onChange={changeHandler} />
          <ThreesomeFemale onChange={changeHandler} />
          <ThreesomeMale onChange={changeHandler} />
          <Group onChange={changeHandler} />
          <Incest onChange={changeHandler} />
          <Toys onChange={changeHandler} />
          <Anal onChange={changeHandler} />
          <TransGender onChange={changeHandler} />
          <HotWife onChange={changeHandler} />
          <Cuckold onChange={changeHandler} />
          <BDSM onChange={changeHandler} />
          <Rape onChange={changeHandler} />
        </div>
        <InputSubmit value="Upload" spinner={spinner} spinning={"Uploading"} />
      </form>
      {feedback && <ToastAlert str={feedback} />}
    </FullScreenArrowGoBack>
  );
}

export default Upload;
