import { getDatabase, ref, set } from "@firebase/database";
import app from "../account/profile/contextconfig/Config";
import { useState } from "react";
import { InfoCircle } from "react-bootstrap-icons";
import { Collapse, CollapseControl } from "../../components/layout/Layout";

function UserReport() {
  const [report, setReport] = useState("");
  const [spinner, setSpinner] = useState(false);

  const submitHandler = (e) => {
    e.preventDefault();
    setSpinner(true);
    const db = getDatabase();
    const date = new Date();
    let d = date.getTime();
    set(ref(db, "reports/" + d), { about: report }).then(() => {
      setReport("");
      setSpinner(false);
    });
  };

  return (
    <div className="p-2" title="user feedback and report">
      <CollapseControl
        className="ms-2 py-0 d-block w-100 text-start"
        id="report"
      >
        Feedback {"&"} Report <InfoCircle className="float-end" size={23} />
      </CollapseControl>
      <Collapse id="report">
        <form className="d-flex flex-grow-1 m-1" onSubmit={submitHandler}>
          <input
            className="form-control form-control-sm me-2"
            type="search"
            value={report}
            onChange={(e) => setReport(e.target.value)}
            placeholder="Report & Feedback (max 100)"
            autoFocus={true}
            required={true}
            minLength={"10"}
            maxLength={"100"}
            autoComplete="off"
          />
          <input
            className="btn btn-sm btn-outline-success d-flex align-items-center"
            type="submit"
            disabled={spinner}
          />
        </form>
      </Collapse>
    </div>
  );
}

export default UserReport;
