import { FullScreenArrowGoBack } from "../../components/layout/MyLayout";
import { Search as SearchIcon } from "react-bootstrap-icons";
import { useDb } from "../home/database/DbContext";
import { useState } from "react";
import { useStyle } from "../../components/context/StyleContext";
import MMText from "react-mm-text";
import { Container } from "../../components/layout/Layout";
import PostTitle from "../post/PostTitle";
import { useEffect } from "react";
import { useFavorite } from "../favorite/FavContext";
import { getDatabase, ref, set } from "@firebase/database";
import app from "../account/profile/contextconfig/Config";

function Search() {
  const { title } = useDb();
  const { style } = useStyle();
  const [input, setInput] = useState("");
  const [result, setResult] = useState([]);
  const { favorite, addFavorite, removeFavorite } = useFavorite();
  const db = getDatabase();
  const input2 = input && document.getElementById("different").innerHTML;

  useEffect(() => {
    setResult([]);
    if (input) {
      if (title) {
        let temp = [];
        Object.keys(title).map((key) => {
          if (
            title[key].title.includes(input.trim()) ||
            title[key].title.includes(input2.trim())
          ) {
            temp.push(key);
          }
        });
        setResult(temp);
      }
    }
  }, [input]);

  const changeHandler = (e) => {
    setInput(e.target.value);
  };

  const addHandler = async (id, count) => {
    addFavorite(id).then(() => {
      set(ref(db, "titles/" + id + "/like"), { like: count + 1 });
    });
  };

  const removeHandler = async (id, count) => {
    removeFavorite(id).then(() => {
      set(ref(db, "titles/" + id + "/like"), { like: count - 1 });
    });
  };

  return (
    <FullScreenArrowGoBack
      prompt={false}
      zu={true}
      title={<SearchInput value={input} onChange={changeHandler} />}
    >
      <Container className="mb-3">
        <div className="col-lg-6">
          Your input (Current Font): <span id="current">{input}</span>
        </div>
        <div className="col-lg-6">
          Your input (Different Font):{" "}
          <span id="different">
            <MMText
              text={input}
              detector={"knayi"}
              converter={"knayi"}
              showFont={style.font === "zawgyi" ? "unicode" : "zawgyi"}
            />
          </span>
        </div>
      </Container>
      <Container>
        {result.map((item) => (
          <PostTitle
            key={item}
            post={title[item]}
            alreadyFav={favorite.includes(title[item].id)}
            onAdd={() => addHandler(title[item].id, title[item].like.like)}
            onRemove={() =>
              removeHandler(title[item].id, title[item].like.like)
            }
          />
        ))}
      </Container>
    </FullScreenArrowGoBack>
  );
}

export default Search;

const SearchInput = (props) => {
  return (
    <form className="d-flex flex-grow-1 m-1">
      <input
        className="form-control form-control-sm me-2"
        type="search"
        value={props.value}
        onChange={props.onChange}
        placeholder="Search"
        autoFocus={true}
        autoComplete="off"
        aria-label="Search"
      />
      <button
        className="btn btn-sm btn-outline-success d-flex align-items-center"
        type="submit"
      >
        <SearchIcon />
      </button>
    </form>
  );
};
