import { useEffect, useState } from "react";
import app from "../../account/profile/contextconfig/Config";
import { getDatabase, set, ref, onValue } from "firebase/database";
import { useHistory, useLocation } from "react-router";
import { Burmese } from "../../../components/burmese converter/BurmeseConverter";
import { Input, InputArea, InputSubmit } from "../../../components/form/Form";
import { FullScreenArrowGoBack } from "../../../components/layout/MyLayout";
import { ToastAlert } from "../../../components/utilities/Utilities";
import { useAuth } from "../../account/profile/contextconfig/AuthContext";
import {
  Straight,
  Lesbian,
  Gay,
  ThreesomeFemale,
  ThreesomeMale,
  Group,
  Incest,
  Toys,
  Anal,
  TransGender,
  HotWife,
  BDSM,
  Rape,
  Cuckold,
} from "../../upload/UploadComponents";
import { useDb } from "../../home/database/DbContext";

const initialValue = {
  title: "",
  content: "",
  type: "",
  originalWriter: "",
  mp3: "",
};

function Edit() {
  const query = new URLSearchParams(useLocation().search);
  const postId = query.get("name");
  const [article, setArticle] = useState(initialValue);
  const { title } = useDb({});
  const [type, setType] = useState("");
  const [spinner, setSpinner] = useState(false);
  const [feedback, setFeedback] = useState("");
  const [warn, setWarn] = useState(false);
  const { currentUser } = useAuth();
  const history = useHistory();
  const db = getDatabase();

  useEffect(() => {
    let mount = true;
    const fetchData = async () => {
      onValue(ref(db, "main/" + postId), (snapshot) => {
        const da = snapshot.val();
        if (mount) {
          setArticle(da);
        }
      });
    };
    fetchData();
    if (title) {
      setType(title[postId].type);
    }
    return () => (mount = false);
  }, []);

  useEffect(() => {
    const timeout = setTimeout(() => {
      setFeedback("");
    }, 1369);
    return () => clearTimeout(timeout);
  }, [feedback]);

  async function submitHandler(e) {
    e.preventDefault();
    setWarn(false);
    setSpinner(true);
    setFeedback("");

    if (type === "") {
      setSpinner(false);
      return setFeedback("Type is empty.");
    }

    let temp = { ...title[postId], type: type };
    if (article.mp3 !== "") {
      temp.mp3.mp3 = true;
    }

    try {
      set(ref(db, "main/" + postId), article);
      set(ref(db, "titles/" + postId), temp);
    } catch (error) {
      setSpinner(false);
      setFeedback("An error occurred. Please try again.");
      return console.log(error.code);
    }

    history.goBack();
  }

  const handleChange = (e) => {
    setWarn(true);
    const { name, value } = e.target;
    setArticle({ ...article, [name]: value });
  };

  const changeHandler = (e) => {
    let name = e.target.name;
    if (e.target.checked) {
      if (type.includes(name)) {
        return;
      } else {
        setType(type + name + ", ");
      }
    } else {
      setType(type.replace(name + ", ", ""));
    }
  };

  return (
    <FullScreenArrowGoBack prompt={warn} mode={true} zu={true}>
      <form className="" onSubmit={submitHandler}>
        <Input
          label={<Burmese str="ေခါင္းစဥ္" />}
          name="title"
          value={article && article.title}
          onChange={handleChange}
        />
        <InputArea
          rows="10"
          label={<Burmese str="ဇာတ္လမ္း" />}
          name="content"
          value={article && article.content}
          onChange={handleChange}
        />
        <Input
          label={<Burmese str="မူရင္း ေရးသားသူ" />}
          name="originalWriter"
          value={article && article.originalWriter}
          onChange={handleChange}
        />

        {currentUser && currentUser.email === "whitedark@gmail.com" && (
          <Input
            req={false}
            label="mp3"
            name="mp3"
            value={article && article.mp3}
            onChange={handleChange}
          />
        )}

        <h6>
          <Burmese str="အမ်ိဳးအစား (အနည္းဆံုး တစ္ခုေရြးပါ)" />
        </h6>

        <div className="flex-column flex-md-row d-flex flex-wrap mb-3">
          <Straight
            checked={type && type.includes("Straight")}
            onChange={changeHandler}
          />
          <Lesbian
            checked={type && type.includes("Lesbian")}
            onChange={changeHandler}
          />
          <Gay
            checked={type && type.includes("Gay")}
            onChange={changeHandler}
          />
          <ThreesomeFemale
            checked={type && type.includes("ThreesomeFemale")}
            onChange={changeHandler}
          />
          <ThreesomeMale
            checked={type && type.includes("ThreesomeMale")}
            onChange={changeHandler}
          />
          <Group
            checked={type && type.includes("Group")}
            onChange={changeHandler}
          />
          <Incest
            checked={type && type.includes("Incest")}
            onChange={changeHandler}
          />
          <Toys
            checked={type && type.includes("Toys")}
            onChange={changeHandler}
          />
          <Anal
            checked={type && type.includes("Anal")}
            onChange={changeHandler}
          />
          <TransGender
            checked={type && type.includes("TransGender")}
            onChange={changeHandler}
          />
          <HotWife
            checked={type && type.includes("HotWife")}
            onChange={changeHandler}
          />
          <Cuckold
            checked={type && type.includes("Cuckold")}
            onChange={changeHandler}
          />
          <BDSM
            checked={type && type.includes("BDSM")}
            onChange={changeHandler}
          />
          <Rape
            checked={type && type.includes("Rape")}
            onChange={changeHandler}
          />
        </div>

        <InputSubmit value="Upload" spinner={spinner} spinning={"Uploading"} />
      </form>
      {feedback && <ToastAlert str={feedback} />}
    </FullScreenArrowGoBack>
  );
}

export default Edit;
